/**
 * document ready
 * @author 
 * 
 */
$(function() {
    // 轮播图设置
    if(window.Swiper) {
        var mySwiper = new Swiper ('.swiper-container', {
            direction: 'horizontal',
            autoplay: 5000,
            speed: 1000,
            slidesPerView: 'auto', // 自定义滚动区域宽度
            pagination: '.swiper-pagination',
            nextButton: '.swiper-button-next',
            prevButton: '.swiper-button-prev',
        });   
    }

    // 左栏滚动fixed
    var scrollDistance = [0];
    var fixed = function() {
        var titleHeight = $('.m_blockTitle').height();
        $('.m_blockTitle').each(function(index, item) {
            if(index > 0) {
                // 首次加载，scrollDistance需重新设置
                if(scrollDistance.length !== $('.m_blockTitle').length) {
                    var itemTop = $(item).offset().top,
                        itemTopFixed = titleHeight*index,
                        scrollItem = itemTop - itemTopFixed;
                    scrollDistance.push(scrollItem);
                }

                if($(window).scrollTop() >= scrollDistance[index]) {
                    // console.log('fixed' + index);
                    $(item).css({
                        'position': 'fixed',
                        'top': titleHeight + 'px',
                        'left': 'auto'
                    })
                } else {
                    // console.log('absolute' + index);
                    $(item).css({
                        'position': 'absolute',
                        'top': '0'
                    })
                }
            }
        })
    }
    fixed();

    $(window).scroll(function() {
        fixed();
    })
});


